.header__burger-inner_active {
  position: relative;
  height: 3px;
  width: 100%;
  transform: rotate(135deg) scaleX(120%);
}

.header__burger-inner_active::after {
  content: '';
  position: absolute;
  top: 9px;
  left: 0;
  height: 3px;
  width: 25px;
  background-color: var(--color-white);
  transform: translateY(-9px);
}

.header__burger-inner_active::before {
  content: '';
  position: absolute;
  top: -9px;
  left: 0;
  height: 3px;
  width: 25px;
  background-color: var(--color-white);
  transform: rotate(90deg) translateX(9px) scaleX(120%);
}
